import React, { useEffect, useState, useRef} from "react";
import{Html5Qrcode} from "html5-qrcode";

const API_BASE = "/api";

function App() {
  const [stores, setStores] = useState([]);
  const [selectedStoreId, setSelectedStoreId] = useState("");
  const [items, setItems] = useState([]);
  const [reorderList, setReorderList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  // --- New store form state ---
  const [newStoreName, setNewStoreName] = useState("");
  const [newStoreLocation, setNewStoreLocation] = useState("");

  // --- New item form state ---
  const [itemName, setItemName] = useState("");
  const [itemSku, setItemSku] = useState("");
  const [itemBarcode, setItemBarcode] = useState("");
  const [itemUnit, setItemUnit] = useState("");
  const [itemCurrentStock, setItemCurrentStock] = useState("");
  const [itemReorderLevel, setItemReorderLevel] = useState("");
  const [itemTargetStock, setItemTargetStock] = useState("");

    // --- Barcode scan / lookup state ---
    const [barcodeQuery, setBarcodeQuery] = useState("");
    const [scannedItem, setScannedItem] = useState(null);
    const [scanMessage, setScanMessage] = useState("");
      // --- Stock adjustment for scanned item ---
  const [transactionQty, setTransactionQty] = useState("");
  const [transactionMessage, setTransactionMessage] = useState("");

  const scannerRef = useRef(null);
  const [isScanning, setIsScanning] = useState(false);

  // Load stores on first render
  useEffect(() => {
    loadStores();
  }, []);

  async function loadStores() {
    try {
      setLoading(true);
      setError("");

      const res = await fetch(`${API_BASE}/stores`);
      if (!res.ok) {
        throw new Error(`Failed to load stores: ${res.status}`);
      }
      const data = await res.json();
      setStores(data);

      if (data.length > 0) {
        const firstId = data[0].id;
        setSelectedStoreId(String(firstId));
        await loadStoreData(firstId);
      } else {
        setSelectedStoreId("");
        setItems([]);
        setReorderList([]);
      }
    } catch (err) {
      setError(err.message || "Error loading stores");
    } finally {
      setLoading(false);
    }
  }

  async function loadStoreData(storeId) {
    try {
      setLoading(true);
      setError("");

      // Load items for the store
      const itemsRes = await fetch(`${API_BASE}/stores/${storeId}/items`);
      if (!itemsRes.ok) {
        throw new Error(`Failed to load items: ${itemsRes.status}`);
      }
      const itemsData = await itemsRes.json();
      setItems(itemsData);

      // Load reorder list for the store
      const reorderRes = await fetch(
        `${API_BASE}/stores/${storeId}/reorder-list`
      );
      if (!reorderRes.ok) {
        throw new Error(
          `Failed to load reorder list: ${reorderRes.status}`
        );
      }
      const reorderData = await reorderRes.json();
      setReorderList(reorderData);
    } catch (err) {
      setError(err.message || "Error loading store data");
    } finally {
      setLoading(false);
    }
  }

  function handleStoreChange(e) {
    const value = e.target.value;
    setSelectedStoreId(value);
    if (value) {
      const numericId = Number(value);
      if (!Number.isNaN(numericId)) {
        loadStoreData(numericId);
      }
    } else {
      setItems([]);
      setReorderList([]);
    }
  }

  // --- Create store from UI ---
  async function handleCreateStore(e) {
    e.preventDefault();

    if (!newStoreName.trim()) {
      setError("Store name is required");
      return;
    }

    try {
      setLoading(true);
      setError("");

      const res = await fetch(`${API_BASE}/stores`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: newStoreName.trim(),
          location: newStoreLocation.trim(),
          isActive: true,
        }),
      });

      if (!res.ok) {
        throw new Error(`Failed to create store: ${res.status}`);
      }

      const created = await res.json();

      // Add to list locally
      setStores((prev) => [...prev, created]);

      // Select this store and load its data
      const newId = created.id;
      setSelectedStoreId(String(newId));
      setNewStoreName("");
      setNewStoreLocation("");
      await loadStoreData(newId);
    } catch (err) {
      setError(err.message || "Error creating store");
    } finally {
      setLoading(false);
    }
  }

  // --- Create item from UI ---
  async function handleCreateItem(e) {
    e.preventDefault();

    if (!selectedStoreId) {
      setError("Please select a store first");
      return;
    }
    if (!itemName.trim()) {
      setError("Item name is required");
      return;
    }
    if (!itemSku.trim()) {
      setError("Item SKU is required");
      return;
    }

    const storeIdNum = Number(selectedStoreId);

    try {
      setLoading(true);
      setError("");

      const res = await fetch(`${API_BASE}/items`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          name: itemName.trim(),
          sku: itemSku.trim(),
          barcode: itemBarcode.trim() || null,
          unit: itemUnit.trim() || "unit",
          currentStock: itemCurrentStock ? Number(itemCurrentStock) : 0,
          reorderLevel: itemReorderLevel ? Number(itemReorderLevel) : 0,
          targetStock: itemTargetStock ? Number(itemTargetStock) : 0,
          isActive: true,
          storeId: storeIdNum,
        }),
      });

      if (!res.ok) {
        throw new Error(`Failed to create item: ${res.status}`);
      }

      // Reload data for this store so tables update
      await loadStoreData(storeIdNum);

      // Clear form
      setItemName("");
      setItemSku("");
      setItemBarcode("");
      setItemUnit("");
      setItemCurrentStock("");
      setItemReorderLevel("");
      setItemTargetStock("");
    } catch (err) {
      setError(err.message || "Error creating item");
    } finally {
      setLoading(false);
    }
  }
  // --- Lookup item by barcode (works with scanner or keyboard) ---
    async function lookupBarcode(value) {
    setScannedItem(null);
    setScanMessage("");

    if (!selectedStoreId) {
      setError("Please select a store before scanning.");
      return;
    }
    if (!value.trim()) {
      setError("Please enter or scan a barcode.");
      return;
    }

    const storeIdNum = Number(selectedStoreId);

    try {
      setLoading(true);
      setError("");

      const res = await fetch(
        `${API_BASE}/stores/${storeIdNum}/items/by-barcode/${encodeURIComponent(
          value.trim()
        )}`
      );

      if (res.status === 404) {
        setScanMessage("No item found for this barcode in this store.");
        setScannedItem(null);
        return;
      }

      if (!res.ok) {
        throw new Error(`Lookup failed: ${res.status}`);
      }

      const data = await res.json();
      setScannedItem(data);
      setScanMessage("");
    } catch (err) {
      setError(err.message || "Error during barcode lookup");
    } finally {
      setLoading(false);
    }
  }

  async function handleBarcodeLookup(e) {
    e.preventDefault();
    await lookupBarcode(barcodeQuery);
  }
  async function startCameraScan() {
    if (!selectedStoreId) {
      setError("Please select a store before scanning.");
      return;
    }
    if (isScanning) {
      return;
    }

    try {
      setError("");
      setScanMessage("Starting camera...");

      // 1) Find available cameras first
      const cameras = await Html5Qrcode.getCameras();
      if (!cameras || cameras.length === 0) {
        setError("No camera device found on this system.");
        setScanMessage("");
        return;
      }

      // Pick the first camera
      const cameraId = cameras[0].id;

      const html5QrCode = new Html5Qrcode("barcode-reader");
      scannerRef.current = html5QrCode;
      setIsScanning(true);

      await html5QrCode.start(
        cameraId,
        { fps: 10, qrbox: { width: 250, height: 250 } },
        async (decodedText) => {
          setBarcodeQuery(decodedText);
          setScanMessage(`Scanned: ${decodedText}`);
          await lookupBarcode(decodedText);
        },
        (errorMessage) => {
          // Continuous scan errors can be safely ignored or logged
          // console.log("scan error", errorMessage);
        }
      );
    } catch (err) {
      console.error("Camera start error:", err);
      setError(
        "Camera error: " +
          (err && err.message ? err.message : String(err || "Unknown error"))
      );
      setIsScanning(false);
      setScanMessage("");
      if (scannerRef.current) {
        try {
          await scannerRef.current.stop();
          scannerRef.current.clear();
        } catch (e) {
          // ignore further errors
        }
        scannerRef.current = null;
      }
    }
  }


  async function stopCameraScan() {
    if (scannerRef.current) {
      try {
        await scannerRef.current.stop();
        scannerRef.current.clear();
      } catch (err) {
        console.error(err);
      }
      scannerRef.current = null;
    }
    setIsScanning(false);
    setScanMessage("");
  }


  async function handleSale() {
    setTransactionMessage("");
    if (!scannedItem) {
      setError("Scan an item first.");
      return;
    }
    const qty = Number(transactionQty);
    if (!qty || qty <= 0) {
      setError("Enter a positive quantity.");
      return;
    }

    try {
      setLoading(true);
      setError("");

      const res = await fetch(`${API_BASE}/transactions/sale`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          itemId: scannedItem.id,
          quantity: qty,
        }),
      });

      if (!res.ok) {
        const text = await res.text();
        throw new Error(text || `Sale failed: ${res.status}`);
      }

      const updatedItem = await res.json();
      setScannedItem(updatedItem);
      setTransactionMessage(`Sold ${qty} units.`);
      setTransactionQty("");

      const storeIdNum = Number(selectedStoreId);
      if (!Number.isNaN(storeIdNum)) {
        await loadStoreData(storeIdNum); // refresh inventory + reorder list
      }
    } catch (err) {
      setError(err.message || "Error while recording sale");
    } finally {
      setLoading(false);
    }
  }

  async function handlePurchase() {
    setTransactionMessage("");
    if (!scannedItem) {
      setError("Scan an item first.");
      return;
    }
    const qty = Number(transactionQty);
    if (!qty || qty <= 0) {
      setError("Enter a positive quantity.");
      return;
    }

    try {
      setLoading(true);
      setError("");

      const res = await fetch(`${API_BASE}/transactions/purchase`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          itemId: scannedItem.id,
          quantity: qty,
        }),
      });

      if (!res.ok) {
        const text = await res.text();
        throw new Error(text || `Purchase failed: ${res.status}`);
      }

      const updatedItem = await res.json();
      setScannedItem(updatedItem);
      setTransactionMessage(`Added ${qty} units.`);
      setTransactionQty("");

      const storeIdNum = Number(selectedStoreId);
      if (!Number.isNaN(storeIdNum)) {
        await loadStoreData(storeIdNum);
      }
    } catch (err) {
      setError(err.message || "Error while recording purchase");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div style={{ padding: "1.5rem", fontFamily: "system-ui, sans-serif" }}>
      <h1>Inventory Dashboard</h1>
      <p>Backend: {API_BASE}</p>

      {error && (
        <div
          style={{
            backgroundColor: "#ffe5e5",
            color: "#900",
            padding: "0.5rem 1rem",
            marginBottom: "1rem",
            borderRadius: "4px",
          }}
        >
          {error}
        </div>
      )}

      {loading && <p>Loading...</p>}

      {/* Add Store Form */}
      <section
        style={{
          marginBottom: "1.5rem",
          padding: "1rem",
          border: "1px solid #ddd",
          borderRadius: "6px",
        }}
      >
        <h2>Add Store</h2>
        <form onSubmit={handleCreateStore}>
          <div style={{ marginBottom: "0.5rem" }}>
            <label>
              Name:&nbsp;
              <input
                type="text"
                value={newStoreName}
                onChange={(e) => setNewStoreName(e.target.value)}
              />
            </label>
          </div>
          <div style={{ marginBottom: "0.5rem" }}>
            <label>
              Location:&nbsp;
              <input
                type="text"
                value={newStoreLocation}
                onChange={(e) => setNewStoreLocation(e.target.value)}
              />
            </label>
          </div>
          <button type="submit">Create Store</button>
        </form>
      </section>

      {/* Store Selector */}
      <section style={{ marginBottom: "1.5rem" }}>
        <h2>Stores</h2>
        {stores.length === 0 ? (
          <p>No stores found. Use the form above to add one.</p>
        ) : (
          <label>
            Select store:{" "}
            <select value={selectedStoreId} onChange={handleStoreChange}>
              {stores.map((store) => (
                <option key={store.id} value={store.id}>
                  {store.name} (#{store.id})
                </option>
              ))}
            </select>
          </label>
        )}
      </section>

      {/* Add Item Form */}
<section
  style={{
    marginBottom: "1.5rem",
    padding: "1rem",
    border: "1px solid #ddd",
    borderRadius: "6px",
  }}
>
  <h2>Add Item to Selected Store</h2>
  {selectedStoreId ? (
    <form onSubmit={handleCreateItem}>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          Name:&nbsp;
          <input
            type="text"
            value={itemName}
            onChange={(e) => setItemName(e.target.value)}
          />
        </label>
      </div>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          SKU:&nbsp;
          <input
            type="text"
            value={itemSku}
            onChange={(e) => setItemSku(e.target.value)}
          />
        </label>
      </div>
      <div
        id="barcode-reader"
        style={{
          width: "260px",
          maxWidth: "100%",
          marginBottom: "0.5rem",
          display: isScanning ? "block" : "none",
        }}
      ></div>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          Unit:&nbsp;
          <input
            type="text"
            value={itemUnit}
            onChange={(e) => setItemUnit(e.target.value)}
            placeholder="bottle, pack, etc."
          />
        </label>
      </div>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          Current Stock:&nbsp;
          <input
            type="number"
            value={itemCurrentStock}
            onChange={(e) => setItemCurrentStock(e.target.value)}
          />
        </label>
      </div>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          Reorder Level:&nbsp;
          <input
            type="number"
            value={itemReorderLevel}
            onChange={(e) => setItemReorderLevel(e.target.value)}
          />
        </label>
      </div>
      <div style={{ marginBottom: "0.5rem" }}>
        <label>
          Target Stock:&nbsp;
          <input
            type="number"
            value={itemTargetStock}
            onChange={(e) => setItemTargetStock(e.target.value)}
          />
        </label>
      </div>
      <button type="submit">Create Item</button>
    </form>
  ) : (
    <p>Please select a store above before adding items.</p>
  )}
</section>
            {/* Barcode Scan / Lookup */}
            <section
        style={{
          marginBottom: "1.5rem",
          padding: "1rem",
          border: "1px solid #ddd",
          borderRadius: "6px",
        }}
      >
        <h2>Scan / Enter Barcode</h2>
        {selectedStoreId ? (
          <>
            <form onSubmit={handleBarcodeLookup} style={{ marginBottom: "0.5rem" }}>
              <label>
                Barcode:&nbsp;
                <input
                  type="text"
                  value={barcodeQuery}
                  onChange={(e) => setBarcodeQuery(e.target.value)}
                  placeholder="Scan or type barcode"
                />
              </label>
              &nbsp;
              <button type="submit">Lookup</button>
            </form>
            <div style={{ marginBottom: "0.5rem" }}>
              <button
                type="button"
                onClick={isScanning ? stopCameraScan : startCameraScan}
              >
                {isScanning ? "Stop Camera" : "Scan with Camera"}
              </button>
            </div>

            {isScanning && (
              <div
                id="barcode-reader"
                style={{ width: "260px", maxWidth: "100%", marginBottom: "0.5rem" }}
              />
            )}

            {scanMessage && <p>{scanMessage}</p>}

            {scannedItem && (
              <div style={{ padding: "0.5rem 0" }}>
                <strong>Item:</strong> {scannedItem.name} ({scannedItem.sku})
                <br />
                <strong>Current stock:</strong> {scannedItem.currentStock}
                <br />
                <strong>Reorder level:</strong> {scannedItem.reorderLevel}
                <br />
                <strong>Target stock:</strong> {scannedItem.targetStock}

                <div style={{ marginTop: "0.5rem" }}>
                  <label>
                    Quantity:&nbsp;
                    <input
                      type="number"
                      value={transactionQty}
                      onChange={(e) => setTransactionQty(e.target.value)}
                      style={{ width: "80px" }}
                    />
                  </label>
                  &nbsp;
                  <button type="button" onClick={handleSale}>
                    Sell
                  </button>
                  &nbsp;
                  <button type="button" onClick={handlePurchase}>
                    Add Stock
                  </button>
                </div>

                {transactionMessage && (
                  <p style={{ marginTop: "0.25rem" }}>{transactionMessage}</p>
                )}
              </div>
            )}

          </>
        ) : (
          <p>Please select a store above before scanning.</p>
        )}
      </section>

      {/* Inventory Table */}
      <section style={{ marginBottom: "1.5rem" }}>
        <h2>Inventory</h2>
        {items.length === 0 ? (
          <p>No items for this store.</p>
        ) : (
          <table
            style={{
              borderCollapse: "collapse",
              width: "100%",
              maxWidth: "800px",
            }}
          >
            <thead>
              <tr>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "left", padding: "0.5rem" }}>Name</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "left", padding: "0.5rem" }}>SKU</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Stock</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Reorder Level</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Target Stock</th>
              </tr>
            </thead>
            <tbody>
              {items.map((item) => (
                <tr key={item.id}>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem" }}>{item.name}</td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem" }}>{item.sku}</td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {item.currentStock}
                  </td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {item.reorderLevel}
                  </td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {item.targetStock}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </section>

      {/* Reorder List Table */}
      <section>
        <h2>Reorder List</h2>
        {reorderList.length === 0 ? (
          <p>No items need to be reordered for this store.</p>
        ) : (
          <table
            style={{
              borderCollapse: "collapse",
              width: "100%",
              maxWidth: "800px",
            }}
          >
            <thead>
              <tr>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "left", padding: "0.5rem" }}>Item</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Current</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Target</th>
                <th style={{ borderBottom: "1px solid #ccc", textAlign: "right", padding: "0.5rem" }}>Reorder Qty</th>
              </tr>
            </thead>
            <tbody>
              {reorderList.map((row) => (
                <tr key={row.itemId}>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem" }}>{row.name}</td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {row.currentStock}
                  </td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {row.targetStock}
                  </td>
                  <td style={{ borderBottom: "1px solid #eee", padding: "0.5rem", textAlign: "right" }}>
                    {row.reorderQuantity}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </section>
    </div>
  );
}

export default App;
